<script>
import footerBar from '@/components/footerBar'
import api from '@/utils/api'
import {mapMutations, mapState} from 'vuex'

export default {
    data () {
      return {
        loginType: 0,
        formInfo: {
          phone: '',
          code: '',
          phrase: '',
          password: ''
        },
        countDown: 0,
        error: ''
      }
    },
    components: {
        footerBar
    },
    computed: {
      ...mapState('account', [
        'phrase_url',
        '_version'
      ])
    },
    mounted () {
      this.changePhrasePath()
      document.title = '超多客登录'
    },
    methods: {
      ...mapMutations('account', [
        'changePhrasePath'
      ]),
      ...mapMutations('pc', [
        'updateJumpToJoin'
      ]),
      jumpToJoin () {
        console.log(this.$router)
        this.$router.push('/?jumpToJoin=1')
      },
      submit () {
        api.cdkLogin({
          type: this.loginType,
          ...this.formInfo,
          _version: this._version
        }).then(res => res.data).then(data => {
          if (data.code === 200) {
            location.href = '/'
          } else {
            this.error = data.message
          }
        })
      },
      switchLoginType () {
        this.loginType = this.loginType === 0 ? 1 : 0
      },
      cdkSendSms () {
        console.log(this)
        if (this.countDown > 0 || !this.formInfo.phone || !/^1[3-9][0-9][\d]{8}$/.test(this.formInfo.phone)) {
          return
        }
        api.cdkSendSms({
          mobile: this.formInfo.phone
        }).then(res => res.data).then(data => {
          if (data.code === 200) {
            this.startCountDown()
          }
        })
      },
      startCountDown () {
        this.countDown = 60
        let si = setInterval(() => {
          this.countDown--
          if (this.countDown < 1) {
            clearInterval(si)
          }
        }, 1000);
      }
    }
}
</script>


